




































































































































































import Vue from "vue";
import { mapState, mapActions } from "vuex";
import DarkModeToggle from "@/components/DarkModeToggle.vue";
import LoginModal from "@/components/LoginModal.vue";
import Sidebar from "@/layout/Sidebar.vue";
import Error from "@/layout/Error.vue";
import DropDown from "@/components/DropDown.vue";

export default Vue.extend({
  name: "Navbar",
  components: {
    DarkModeToggle,
    LoginModal,
    Sidebar,
    Error,
    DropDown,
  },
  data: function () {
    return {
      routeText: "" as string,
      scroll: document.documentElement.scrollTop,
      perfersDark: window.matchMedia("(prefers-color-scheme: dark)").matches,
    };
  },
  computed: {
    ...mapState("AuthState", ["isAuthenticated", "isAuthLoading"]),
    ...mapState("PostState", ["isPostLoading"]),
    ...mapState("SubState", ["isSubLoading", "sub"]),
    ...mapState("DarkMode", ["isDarkMode"]),
    ...mapState("MediaQueryState", ["mq"]),
    ...mapState("EventState", ["loginModal", "sidebar"]),
  },
  methods: {
    ...mapActions("AuthState", ["logout", "me"]),
    ...mapActions("DarkMode", ["turnOn"]),
    ...mapActions("EventState", [
      "openLoginModal",
      "closeLoginModal",
      "openSidebar",
      "closeSidebar",
    ]),
    handleLogout() {
      this.logout;
    },
  },
  watch: {
    $route(to, from) {
      if (to.path != from.path && to.params.sub) {
        this.routeText = to.params.sub;
      } else {
        this.routeText = to.name;
      }
    },
  },
  mounted: function () {
    document.addEventListener(
      "scroll",
      () => (this.scroll = document.documentElement.scrollTop)
    );

    if (this.$router.currentRoute.params.sub) {
      this.routeText = this.$router.currentRoute.params.sub;
    } else if (this.$router.currentRoute.name) {
      this.routeText = this.$router.currentRoute.name;
    }

    if (this.perfersDark) this.turnOn();

    this.me();
  },
});
