










import Vue from "vue";
import { mapActions } from "vuex";

export default Vue.extend({
  name: "DropDown",
  data: function () {
    return {
      isOpen: false,
    };
  },
  methods: {
    ...mapActions("EventState", ["openLoginModal"]),
    ...mapActions("AuthState", ["logout"]),
    handleClick() {
      this.isOpen ? (this.isOpen = false) : (this.isOpen = true);
    },
  },
});
