








import Vue from "vue";
import { mapActions, mapState } from "vuex";
import SubCard from "@/components/SubCard.vue";

export default Vue.extend({
  name: "Home",
  components: {
    SubCard,
  },
  computed: {
    ...mapState("SubState", ["subsArr"]),
  },
  methods: {
    ...mapActions("SubState", ["getSubs"]),
  },
  mounted: async function () {
    this.getSubs();
  },
});
