










import Vue from "vue";
import { mapState, mapActions } from "vuex";

const Component = Vue.extend({
  name: "DarkModeToggle",
  computed: mapState("DarkMode", ["isDarkMode"]),
  methods: {
    ...mapActions("DarkMode", ["turnOn", "turnOff"]),
    toggleDarkMode() {
      this.isDarkMode ? this.turnOff() : this.turnOn();
    },
  },
});
export default Component;
