















































import Vue from "vue";
import { mapState, mapActions } from "vuex";
import About from "./About.vue";
import Post from "./Post.vue";

export default Vue.extend({
  name: "Content",
  components: {
    About,
    Post,
  },
  data: function () {
    return {
      sort: "points",
      order: -1,
    };
  },
  computed: mapState("SubState", ["sub", "posts"]),
  methods: {
    ...mapActions("SubState", ["getPostsBySubName"]),
  },
  watch: {
    sort: function () {
      this.getPostsBySubName({
        sub: this.$route.params.sub,
        sort: this.sort,
        order: this.order,
      });
    },
  },
});
